
import CommonHeader from '@/pages/common/header.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


import golfConfirm from '@/pages/golf/golfConfirm'

export default {
name: 'Course',
data () {
return {
value: '',
position: [],
caddie: ['球场安排', '球场安排', '球场安排', '球场安排'],
caddies: ['不需要', '球场安排', '男球童', '女球童'],
radios: ['我接受并组', '我不接受并组,如有冲突,请通知我更改时间', '我不接受并组,并支付四个球位的费用'],
radio: '我接受并组',
msg: '请选择日期',
show: false,
showConfirm: false
}
},
computed: {
caddiesIsShow () {
const caddie = [false, false, false, false]
this.position.forEach(element => {
caddie[element - 1] = true
})
return caddie
}
},
methods: {
onChange (val) {
console.log(val)
},
handlePositionChange (val) {
console.log(val)
},
handleConcat (val) {
const currentPostion = this.position
if (val === '我不接受并组,并支付四个球位的费用') {
this.position = [1, 2, 3, 4]
} else {
this.position = currentPostion
}
},
handleNextStep () {
if (this.value === '') {
this.msg = '请选择日期'
this.show = true
} else if (this.position.length === 0) {
this.msg = '请选择球位'
this.show = true
} else {
this.showConfirm = true
}
},
handleHideConfirm () {
this.showConfirm = false
},
handleReset () {
this.value = ''
this.position = []
this.caddie = ['球场安排', '球场安排', '球场安排', '球场安排']
this.radio = '我接受并组'
}
},
components: {
CommonHeader,
Datetime,
Group,
Checker,
CheckerItem,
PopupRadio,
Radio,
Toast,
golfConfirm
},
activated () {
console.log('res')
this.value = ''
this.position = []
this.caddie = ['球场安排', '球场安排', '球场安排', '球场安排']
this.radio = '我接受并组'
}
}



